            "lyrics": "韩国独立小清新家庭手工业，带给你一天好心情，跟着调调摇头晃脑吧！好甜好甜呀！\r\n\r\n\r\n歌曲：우리만나요 (샤이니의 월드데이트 삽입곡) 我们见面吧\r\n艺人：家庭手工业\r\n专辑：우리만나요\r\n\r\n우리 만나요\r\n我们见面吧\r\n너무나 오래 기다린 순간.\r\n这等待已久的瞬间\r\n별은 하나 둘씩\r\n星星一颗两颗浮现\r\n떠올라 마음 설레게 해요.\r\n让人心动\r\n\r\n\r\n우리 만나요\r\n我们见面吧\r\n빛나는 그대 처음본 순간\r\n那瞬间第一次看到耀眼的你\r\n창가에 앉아\r\n坐在窗边\r\n떠올리면 가슴이 떨려\r\n一想起你心就不停跳\r\n\r\n\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n\r\n\r\n그댄 빛나요\r\n你太耀眼\r\n요즘에 난 너무 행복해\r\n最近我太幸福\r\n그대 꿈을 꾸면\r\n如果梦到你\r\n자다가도 웃음이 터져\r\n睡着了也会笑出声\r\n\r\n\r\n그댄 빛나요\r\n你太耀眼\r\n밤하늘 별도 너무 반짝여\r\n夜晚的星星也太闪耀\r\n둘이 손 꼭 잡고\r\n想要牵着你的手\r\n별빛 아래 걷고 싶어요\r\n在星光下漫步\r\n\r\n\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩닥콩닥콩닥콩닥 하나, 둘, 셋, 넷!\r\n空咚空咚空咚空咚 1，2，3，4！\r\n\r\n\r\n우리 만나요.\r\n我们见面吧\r\n두근 거려 잠이 안-와요.\r\n心怦怦跳 睡不着觉\r\n많이 사랑해요.\r\n好爱你啊\r\n빨리 달려와 안-아-줘-요\r\n快点跑来 拥抱我吧\r\n\r\n\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n\r\n\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩닥콩닥콩닥콩닥 둘, 둘, 셋, 넷!\r\n空咚空咚空咚空咚 2，2，3，4！\r\n\r\n\r\n우리 만나요.\r\n我们见面吧\r\n두근 거려 잠이 안-와요.\r\n心怦怦跳 睡不着觉\r\n많이 사랑해요.\r\n好爱你啊\r\n빨리 달려와 안-아-줘-요\r\n快点跑来 拥抱我吧\r\n\r\n\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n\r\n\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩닥콩닥콩닥콩닥\r\n扑通扑通扑通扑通\r\n\r\n\r\n그댄 너무 멀리 살고,\r\n你住在太遥远的地方\r\n서로 한 마디 못하지만\r\n虽然彼此说不上话\r\n그대 빛나는 눈빛\r\n你发光的眼神\r\n내 맘 사로 잡았죠. 셋, 넷!\r\n勾走了我的心啊 3，4！\r\n\r\n\r\n우리 만나요.\r\n我们见面吧\r\n두근 거려 잠이 안-와요.\r\n心怦怦跳 睡不着觉\r\n많이 사랑해요.\r\n好爱你啊\r\n빨리 달려와 안-아-줘-요.\r\n快点跑来 拥抱我吧\r\n\r\n\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n\r\n\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩닥콩닥콩닥콩닥 두근두근두근두근\r\n空咚空咚空咚空咚 扑通扑通扑通扑通\r\n콩!\r\n空！",
